import Level from './level'
import React from 'react'
import Map from './map'

class Game extends React.Component{
    constructor(props){
        super(props);
        //row col for new map
        this.map = new Map(100, 80)
        var playerPositions = this.map.playerSquares

    }

    render(){
        return(
            <div>
                <Level map={this.map} pixelSize={8}/>
            </div>
           
           
        );
    }

  }
   
  export default Game